import {Injectable} from '@angular/core';
import {Camera, CameraOptions} from '@ionic-native/camera';
import { AlertController, Platform} from 'ionic-angular';
import { DomSanitizer, SafeUrl } from '@angular/platform-browser';


@Injectable()
export class ImgService {

  /**
   * 
   * @type {{quality: number, destinationType: number, encodingType: number, mediaType: number}}
     */
  private cameraOptions: CameraOptions = {
    quality: 75,
    destinationType: this.camera.DestinationType.DATA_URL,
    encodingType: this.camera.EncodingType.JPEG,
    sourceType:this.camera.PictureSourceType.CAMERA,
    mediaType: this.camera.MediaType.PICTURE,
    targetWidth: 500,
    targetHeight: 500,
    allowEdit: true,
    saveToPhotoAlbum: false
  }

  /**
   * 
   * @type {ImageResizerOptions}
     */
  private libraryOptions = {
    quality: 75,
    destinationType: this.camera.DestinationType.DATA_URL,
    sourceType:this.camera.PictureSourceType.PHOTOLIBRARY,
    mediaType: this.camera.MediaType.PICTURE,
    allowEdit: true,
    encodingType: this.camera.EncodingType.JPEG,
    targetWidth: 500,
    targetHeight: 500,
    saveToPhotoAlbum: false
  }

  constructor(private camera: Camera, public alertCtrl: AlertController, private sanitize:DomSanitizer, public platform: Platform) {
  }

  /**
   * 
   * @param success
     */
  startCamera(success: Function) {
    this.camera.getPicture(this.cameraOptions).then((imageData) => {
      success(this.encodeAdd(imageData));
    }, (err) => {
      console.log(err);
      //this.failError("");
    });
  }

  /**
   * 
   * @param success
     */
  startPhotoLibrary(success: Function) {
    this.camera.getPicture(this.libraryOptions).then((imageData) => {
      // alert(imageData);
      success(this.encodeAdd(imageData));
    }, (err) => {
      console.log(err);
      //this.failError("");
    });
  }

  /**
   * 
   * @param success
     */
  chooseCamera (success: Function) {
    let alert = this.alertCtrl.create({
      title: '',
      inputs: [
        {
          type : 'radio',
          label: '',
          value: '0',
        },
        {
          type : 'radio',
          label: '',
          value: '1',
        }
      ],
      buttons: [
        {
          text: '',
          role: 'cancel'
        },
        {
          text: '',
          handler: data => {
           console.log(data);
            if (data == 0) {
              if (this.platform.is("android")) {
                this.alertCtrl.create({
                  title: '',
                  subTitle : '4',
                  buttons : [
                    {
                      text: '',
                      role: 'cancel'
                    },
                    {
                      text: '',
                      handler: data => {
                        this.startPhotoLibrary(success);
                      }
                    }
                  ]
                }).present();
              } else {
                this.startPhotoLibrary(success);
              }
            } else if (data == 1) {
              this.startCamera(success);
            }
          }
        }
      ]
    });
    alert.present();
  }

  /**
   * base64
   * @param str
   * @returns {string}
   */
  encodeAdd(str:string):string {
    var newStr = str.replace(/\+/g, "%2B");
    return newStr;
  }

  /**
   * 
   * @param subTitle
     */
  failError(subTitle: string) {
    this.alertCtrl.create({
      title: "",
      subTitle: subTitle,
      buttons: [""]
    }).present();
  }

  /**
   * URL
   * @param image
   */
  safeImage(image: String) : SafeUrl{
    return this.sanitize.bypassSecurityTrustUrl("data:image/jpg;base64," + image)
  }



}



