import {Component, ViewChild} from '@angular/core';
import {IonicApp, Keyboard, Nav, Platform, ToastController} from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

import {StartPage} from "../pages/start/start";
import {TabsPage} from "../pages/tabs/tabs";


@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  //rootPage:any = SearchPage;
  rootPage:any = StartPage;

  backButtonPressed: boolean = false;  //
  @ViewChild('myNav') nav: Nav;

  constructor(public platform: Platform, statusBar: StatusBar, splashScreen: SplashScreen,
              public toastCtrl: ToastController, public keyBoard: Keyboard ,
              public ionicApp: IonicApp,) {
    /*this.showExit();*/

    platform.ready().then(() => {
      statusBar.styleDefault();
      splashScreen.hide();
      this.registerBackButtonAction();//
    });
  }

  //
  registerBackButtonAction() {
    this.platform.registerBackButtonAction(() => {
      if (this.keyBoard.isOpen()) {
        this.keyBoard.close();
      }
      else {
        let activeVC = this.nav.getActive();
        let page = activeVC.instance;
        //ifrootPageelserootPageTabsPageelse
        if (!(page instanceof TabsPage)) {
          if (!this.nav.canGoBack()) {
            console.log("");
            this.showExit();
          }
          else {
            console.log("");
            this.nav.pop();
          }
        }
        else {
          let tabs = page.tabs;
          let activeNav = tabs.getSelected();
          if (!activeNav.canGoBack()) {
            console.log(' tab ');
            this.showExit();
          }
          else {
            console.log(' tab ');
            activeNav.pop();
          }
        }
      }
    }, 1);
  }

  //
  showExit() {
    if (this.backButtonPressed) { //true2APP
      this.platform.exitApp();
    } else {
      this.toastCtrl.create({
        message: "",
        duration: 2000,
        position: 'top',
        cssClass: 'toastcss' //
      }).present();
      this.backButtonPressed = true;
      setTimeout(() => this.backButtonPressed = false, 2000);//2false
    }
  }

  /*  //
    registerBackButtonAction() {
      this.platform.registerBackButtonAction(() => {
        if (this.keyBoard.isOpen()) {
          this.keyBoard.close();
        }
        else {
          let activeVC = this.nav.getActive();
          let page = activeVC.instance;
          //ifrootPageelserootPageTabsPageelse
          if (!(page instanceof TabsPage)) {
            if (!this.nav.canGoBack()) {
              console.log("");
              this.showExit();
            }
            else {
              console.log("");
              this.nav.pop();
            }
          }
          else {
            let tabs = page.tabs;
            let activeNav = tabs.getSelected();
            if (!activeNav.canGoBack()) {
              console.log(' tab ');
              this.showExit();
            }
            else {
              console.log(' tab ');
              activeNav.pop();
            }
          }
        }
      }, 1);
    }

    //
    showExit() {
      if (this.backButtonPressed) { //true2APP
        this.platform.exitApp();
      } else {
        this.toastCtrl.create({
          message: "",
          duration: 2000,
          position: 'bottom'
          //cssClass: 'toastcss' //
        }).present();
        this.backButtonPressed = true;
        setTimeout(() => this.backButtonPressed = false, 2000);//2false
      }
    }*/

}



